.shape-blob {
  background-image: linear-gradient(red, yellow);
  height: 400px;
  width: 400px;
  border-radius: 30% 50% 20% 40%;
  -webkit-animation: transform 20s ease-in-out infinite both alternate,
    movement_one 40s ease-in-out infinite both;
          animation: transform 20s ease-in-out infinite both alternate,
    movement_one 40s ease-in-out infinite both;
  opacity: 0.7;
  position: absolute;
  left: 70%;
  top: 20%;
}
.shape-blob.one {
  height: 300px;
  width: 500px;
  left: -200px;
  top: -150px;
  -webkit-transform: rotate(-380deg);
          transform: rotate(-380deg);
  -webkit-animation: transform 30s ease-in-out infinite both alternate,
    movement_two 60s ease-in-out infinite both;
          animation: transform 30s ease-in-out infinite both alternate,
    movement_two 60s ease-in-out infinite both;
}

.shape-blob.two {
  height: 650px;
  width: 650px;
  left: 80%;
  top: 0%;
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
  -webkit-animation: transform 30s ease-in-out infinite both alternate,
    movement_two 60s ease-in-out infinite both;
          animation: transform 30s ease-in-out infinite both alternate,
    movement_two 60s ease-in-out infinite both;
}

@-webkit-keyframes transform {
  0%,
  100% {
    border-radius: 33% 67% 70% 30% / 30% 30% 70% 70%;
  }
  20% {
    border-radius: 37% 63% 51% 49% / 37% 65% 35% 63%;
  }
  40% {
    border-radius: 36% 64% 64% 36% / 64% 48% 52% 36%;
  }
  60% {
    border-radius: 37% 63% 51% 49% / 30% 30% 70% 70%;
  }
  80% {
    border-radius: 40% 60% 42% 58% / 41% 51% 49% 59%;
  }
}

