/* Style of every tile */
.tile {
  display: inline-block;
  width: 200px;
  height: 100px;
  line-height: 100px;
  -webkit-perspective: 300px;
  -ms-perspective: 300px;
  perspective: 300px;
  /* Style for containing elements */
  /* Style for the hidden link */
}
.tile > div,
.tile a {
  width: 100%;
  color: #30261c;
  background-image: linear-gradient(red, yellow);
  text-align: center;
}
.tile > a {
  display: block;
  text-decoration: none;
  background-color: #d88e8f;
}
/* Flip effect */
.flip > div,
.flip a {
  position: absolute;
}
.flip > a {
  -webkit-transform: rotateX(-90deg);
  transform: rotateX(-90deg);
}
.flip:hover > div {
  -webkit-transform: rotateX(90deg);
  transform: rotateX(90deg);
}
.flip:hover > a {
  -webkit-transform: rotateX(0deg);
  transform: rotateX(0deg);
}
.flip:hover > div,
.flip > a {
  transition: 0.05s all linear 0;
}
.flip:hover > a,
.flip > div {
  transition: 0.05s all linear 0.05s;
}
/* Slide effect */
.slide {
  overflow: hidden;
}
.slide > div,
.slide a {
  transition: 0.1s all linear;
  position: absolute;
}
.slide > a {
  -webkit-transform: translate(200px, 0);
  transform: translate(200px, 0);
}
.slide:hover > div {
  -webkit-transform: translate(-200px, 0);
  transform: translate(-200px, 0);
}
.slide:hover > a {
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
}
/* Shutter effect */
.shutter {
  margin-left: 55px;
  margin-top: 10px;
}
.shutter > div,
.shutter a {
  transition: 0.1s all linear;
  position: absolute;
}
.shutter > div {
  z-index: 10;
  -webkit-transform-origin: 0% 0%;
  transform-origin: 0% 0%;
}
.shutter:hover > div {
  -webkit-transform: rotateX(90deg);
  transform: rotateX(90deg);
}
/* Page effect */
.page > div,
.page a {
  transition: 0.2s all linear;
  position: absolute;
  z-index: 5;
}
.page > div {
  z-index: 10;
  -webkit-transform-origin: 0% 0%;
  transform-origin: 0% 0%;
}
.page:hover > div {
  -webkit-transform: rotateX(360deg);
  transform: rotateX(360deg);
  z-index: 1;
}
.pok {
  font-size: 1.5em;
  color: black;
  font-weight: bold;
}

